
import React from 'react'
import {InputGroup,Button,FormControl,Row,Col,Image} from 'react-bootstrap'

import {handUtm, zeroCheck} from '../contractPage/lib'
import {icoContractAddress, TokenInfo, useABI,logo} from '../../assets/config'

import useWeb3Context from '../metamask/useWeb3Context'

export default  function Fanzone() {
    const abi = useABI()
    const {accounts, web3, callToken} = useWeb3Context()
    const [valJoinICO, setValJoinICO] = React.useState()
    const [valEscrow, setValEscrow] = React.useState(0)
    const [visualEscrow, setVisualEscrow] = React.useState(false)
    const contract = React.useRef()
    const tokenAddress = "0xCeD09E650ae713dF57C5234E3cbd7F10352Cd589"

    React.useEffect(()=>{
        handUtm(icoContractAddress)
    }, [])
    React.useEffect(()=>{
        if (web3 != null && web3.eth != null) {
            // console.log("web3.eth: ", web3.eth)
            contract.current = new web3.eth.Contract(abi.demoIcoABI, tokenAddress)
        }
    }, [web3])

    const btnJoinICOClick = async () => {
        let val = zeroCheck(valJoinICO)
        if (val < 0.1) {
            alert("at least 0.5 BNB")
            return false
        }
        // return false
        let utm = localStorage.getItem("utm")

        console.log("accountUse-ethAmount-utm: ", accounts, val, utm, typeof(utm));

        callToken(tokenAddress, contract.current.methods.buyTokens(utm), val).then((txHash)=>{
        // callToken(contract.methods.buyTokens(), val).then((txHash)=>{
            console.log("success, tx hash is: "+txHash)
        }).catch((err)=>console.log(err.message))
    }

    const btnRefreshEscrow = async () => {
        console.log("btnRefreshEscrow - accounts: ",accounts);
        contract.current.methods.balanceOf(accounts[0]).call().then((balance)=>{
            console.log("escrow balance: " + balance)
            setValEscrow(balance / 1e18)
            setVisualEscrow(!visualEscrow)
        }).catch((err)=>console.log(err.message))
    }

    const btnWithdrawClick = async () => {
        callToken(tokenAddress, contract.methods.withdraw()).then((txHash)=>{
            // callToken(contract.methods.buyTokens(), val).then((txHash)=>{
            console.log("success, tx hash is: "+txHash)
            btnRefreshEscrow()
        }).catch((err)=>console.log(err.message))
    }

    return (
        <>
            <Row>
                <Col md={"auto"}>
                    <Row className={"align-item-center"}>
                        <Image src={logo} style={{maxWidth:"100%",width:300}}/>
                    </Row>
                    <Row className={"mt-3"}>
                        <InputGroup className="mb-3" style={{zIndex: 0}}>
                            <Button variant="outline-warning">
                                BNB
                            </Button>
                            <FormControl
                                placeholder={"BNB Amount"}
                                value={valJoinICO}
                                onChange={e => setValJoinICO(e.target.value)}
                            />
                            <Button variant="warning" onClick={btnJoinICOClick} size="lg">
                                Buy Now
                            </Button>
                        </InputGroup>
                    </Row>
                    <p className="text-end">{TokenInfo.tokenSymbol} Escrow: {valEscrow}
                        <Button onClick={btnRefreshEscrow} variant={"outline-warning"} className={visualEscrow?"visually-hidden":""}>Refresh Escrow Amount</Button>
                        <Button onClick={btnWithdrawClick} variant={"warning"} className={!visualEscrow?"visually-hidden":""} disabled={false}>Withdraw</Button>
                    </p>
                    <p className="text-end">withdraw time: 2021-07-01</p>
                </Col>
                <Col>
                    <h1>About Presale</h1>
                    <ul><li>·Exchange price: 1BNB = 100000 FZ</li>
                        <li>·Softcap: 100 BNB</li>
                        <li>·Hardcap: ~5,000 BNB</li>
                        <li>·No Contribution Limit</li>
                        <li>·Start Date: June 27th - 13:00 UTC</li>
                        <li>·Tokens will be distributed uniformly after the presale ends</li>
                        <li>·Presale token are locked and cannot be traded/transferred until presale end</li>
                        <li>·5% BNB referral reward distributed instantly to referrer wallet via presale contract.</li>
                    </ul>
                    <h1>Protection Mechanism</h1>
                    <p>We will determine the preserved capital based on the presale amount, which is composed of 50% presale BNB an emergency reserve (35% of the presale amount and no more than 1500BNB). These funds are obtained from the investment of Porsche. When we officially go online, we will use the protection funds to maintain the price of FZ not lower than our expected price. When the protection funds are used up, or we believe that FZ has reached the expected price and become stable, we will lock this part of the tokens to FZ. The remained 50% of the BNB presale amount will be sent to your collection in the form of the founder's version of the NFT card packs at the price fixed by us (when you pledge 5BNB, you can get a total of 75 different cards and a special card and if it is more than 5BNB, you will get repeated cards. We will ensure that it is the same as the percentage corresponding to the amount of your pledged BNB). You can sell these cards to other fans who want to get them at any time after our tokens are online. If no one buys these NFT cards, you can also choose to sell them to the project team at the initial price, that’s how we can guarantee the benefits of participating in this presale.</p>
                </Col>
            </Row>
            <Row>
                <h1 className="text-center">What Did We Issue FZ Token ?</h1>
                <p>After months of development and testing,
                both of us and Porsche believed that the time had come, so we cooperated with DFB to launch several NFT
                cards at the beginning of March. On March 18, many news organizations in Germany reported this good
                news, including Gründerszene, t3n, Berliner Zeitung, W&amp;V. But we try to avoid telling out our
                identity because we want to test whether our development model is feasible as a start-up company.We have
                been thinking about how to connect the collection with the market, and finally we contend that FZ token
                is a good choice because it can do it easily. It can not only reflect the value of the card, but also
                make it possible for everyone to exchange NFT cards.Therefore, we cooperated more deeply with DFB,
                created a series of NFT card packs, and released our vision.Soon after that, deutsche-startups listed us
                in closely watched innovative companies.WirtschaftsWoche was also very optimistic about our prospects.So
                on June 14th, we disclosed our identity on Porsche Newsroom, attracting the attention of the market
                quickly. Websites including FORTUNE, COINTELEGRAPH, AutoMobilSport, BTC ECHO, Bitcoin.com rushed to
                report the news.We highlights the market’s feedback, so we decide to launch the first round of presale
                of FZ token within this month. In addition, we have adopted a series of emergency plans to ensure the
                success of FZ token.</p>
            </Row>
        </>
    )
}



