
export function zeroCheck(arg) {
    return (arg==null || arg=="" || arg=='undefined') ? 0 : arg;
}
export function addressCheck(arg) {
    return (arg==null || arg=="" || arg=='undefined' || arg.length!=42) ? false : true;
}
export function getQueryString(name) {
    let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    let r = window.location.search.substr(1).match(reg);
    if (r != null) {
        return decodeURIComponent(r[2]);
    }
    return null;
}
export function handUtm(icoContractAddress) {
    let utm = getQueryString("utm")
    console.log("utm: type: ", utm, typeof(utm), utm=="");
    if (!addressCheck(utm)) {
        utm = localStorage.getItem("utm")
        console.log("utm2: type: ", utm, typeof(utm), utm=="");
        if (!addressCheck(utm)) {
            utm = icoContractAddress
        }
    }
    console.log("utm3: type: ", utm, typeof(utm), utm=="");

    localStorage.setItem("utm", utm)
}



