import React from 'react'
import {InputGroup, Form, Row, Col} from 'react-bootstrap'

export default function TokenERC20MetaData(props) {
    const {formValues, setFormValues} = props;

    React.useEffect(()=>{
        setFormValues(state => ({...state, TokenName:"11", TokenSymbol:"22", TotalSupply: 0}))
    }, [])

    return (
            <Row className="mb-3">
                <Form.Group as={Col} md="4" controlId="validationCustom01">
                    <Form.Label>Token Name</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Token name(eg:Demo Token)"
                        defaultValue={formValues.TokenName}
                        // name={"TokenName"}
                        onChange={evt => {
                            setFormValues(state => ({...state, TokenName: evt.target.value}))
                        }}
                    />
                    <Form.Control.Feedback type="invalid">Please fill the blank of Token Name.</Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustom02">
                    <Form.Label>Token Symbol</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="Token symbol(eg:DT)"
                        defaultValue={formValues.TokenSymbol}
                        // name={"TokenSymbol"}
                        onChange={evt => {
                            setFormValues(state => ({...state, TokenSymbol: evt.target.value}))
                        }}
                    />
                    <Form.Control.Feedback type="invalid">Please fill the blank of Token Symbol.</Form.Control.Feedback>
                </Form.Group>
                <Form.Group as={Col} md="4" controlId="validationCustomUsername">
                    <Form.Label>TotalSupply</Form.Label>
                    <InputGroup hasValidation>
                        {/*<InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>*/}
                        <Form.Control
                            type="number"
                            placeholder="21000000"
                            aria-describedby="inputGroupPrepend"
                            required
                            // name={"TotalSupply"}
                            defaultValue={formValues.TotalSupply}
                            onChange={evt => {
                                setFormValues(state => ({...state, TotalSupply: evt.target.value}))
                            }}
                        />
                        <Form.Control.Feedback type="invalid">Please fill the blank of
                            TotalSupply.</Form.Control.Feedback>
                    </InputGroup>
                </Form.Group>
            </Row>
    );
}



