import MetaMaskOnboarding from '@metamask/onboarding';
import Web3 from "web3";
import {Button} from "react-bootstrap"
import React from "react";

const ONBOARD_TEXT = 'Click here to install MetaMask';
const CONNECT_TEXT = 'Connect';
// const CONNECTED_TEXT = 'Connected';
function shortAddress(res) {
    return (res.length > 13) ? res.substr(0,6) + "..." + res.substr(-4) : res;
}
export default function useWeb3(props) {
    console.log("props222: ", props);
    // const {maskHide} = props
    // console.log("props maskHide: ", maskHide);

    const [buttonText, setButtonText] = React.useState(ONBOARD_TEXT);
    const [isDisabled, setDisabled] = React.useState(false);
    const [accounts, setAccounts] = React.useState([]);
    const web3 = React.useRef(null)

    const onboarding = React.useRef();
    const eth = React.useRef();

    React.useEffect(() => {
        if (!onboarding.current) {
            onboarding.current = new MetaMaskOnboarding();
        }
    }, []);

    React.useEffect(() => {
        if (MetaMaskOnboarding.isMetaMaskInstalled()) {
            if (accounts.length > 0) {
                // setButtonText(CONNECTED_TEXT);
                setButtonText(shortAddress(accounts[0]));
                setDisabled(true);
                onboarding.current.stopOnboarding();
                web3.current = new Web3(window.ethereum || Web3.givenProvider || "http://localhost:8099:8545")
                eth.current = window.ethereum

                if (props!=null && props.onFinish!=null) props.onFinish()
                if (props!=null && props.onEth!=null) props.onEth(eth.current)
            } else {
                setButtonText(CONNECT_TEXT);
                setDisabled(false);
            }
        }
    }, [accounts]);

    React.useEffect(() => {
        function handleNewAccounts(newAccounts) {
            setAccounts(newAccounts);
        }
        if (MetaMaskOnboarding.isMetaMaskInstalled()) {
            window.ethereum.request({ method: 'eth_requestAccounts' }).then(handleNewAccounts);
            window.ethereum.on('accountsChanged', handleNewAccounts);
            return () => {window.ethereum.on('accountsChanged', handleNewAccounts);};
        }
    }, []);

    const onClick = () => {
        if (MetaMaskOnboarding.isMetaMaskInstalled()) {
            window.ethereum.request({ method: 'eth_requestAccounts' }).then((newAccounts) => setAccounts(newAccounts));
        } else {
            onboarding.current.startOnboarding();
        }
    };

    const callToken = async (tokenAddress, fn, eth) => {
        let byteCode = fn.encodeABI();
        console.log("byteCode111: ", byteCode)
        let param = {
            from: accounts[0],
            to: tokenAddress,
            gasPrice: '0x2540be400',    // 10
            gas: '0xdbba0',  // 900000
            // gas: '21000',  // 900000
            data: byteCode
        }
        if (eth != null) {
            let ethAmoutn = '0x' + (parseFloat(eth) * 1e18).toString(16)
            console.log('call with eth: ', eth, ethAmoutn);
            param["value"] = ethAmoutn
        }
        let txHash = window.ethereum.request({method: 'eth_sendTransaction', params: [param]})
        console.log("txHash: ", txHash);
        return txHash
    }

    const OnboardingButton = () => {
        return (<Button disabled={isDisabled} onClick={onClick} variant="outline-light">{buttonText}</Button>)
    }
    return {OnboardingButton, web3: web3.current, eth:eth.current, callToken, accounts};
}



