
import React from 'react'
import {Row,Col,Image,Card,Button,Jumbotron} from 'react-bootstrap'

import {logo, registerEthereumChain,pancakeAddressTest} from '../../assets/config'

import AddTokenToWalletComponent from '../contractPage/AddTokenToWalletComponent'
import ClainBNBComponent from '../contractPage/ClainBNBComponent'
import useWeb3Context from "../metamask/useWeb3Context";
import { BsXDiamondFill,BsGraphUp,BsBarChart,BsExclamationDiamond,BsDropletHalf,BsAward } from "react-icons/bs";
import { MdAttachMoney } from "react-icons/md";
const tokenStyle = [
    {title: "Max Supply",desc:"1,000,000,000,000,000",componet:<BsGraphUp className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"Fixed total Supply and no new token can be minted"},
    {title: "PCS Initial Liquidity",desc:"880,000,000,000,000",componet:<BsBarChart className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"Initial liquidity supply to Pancakeswap for trading"},
    {title: "Transaction Limit",desc:"1,500,000,000,000",componet:<BsExclamationDiamond className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"Inbuilt Anti-Whale mechanism and drastic dump prevention"},
    {title: "BNB Reward",desc:"4 % of every transaction",componet:<BsXDiamondFill className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"BNB reward redistribute to all $HTDog holders passively"},
    {title: "Auto Liquid Generation",desc:"4 % of every transaction",componet:<BsDropletHalf className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"Automatically generating further liquidity on Pancakeswap"},
    {title: "RFI Redistribution",desc:"2 % of every transaction",componet:<MdAttachMoney className={"text-warning"} size={160} />,logo:"https://cryptologos.cc/logos/pancakeswap-cake-logo.svg", content:"HTDog reward redistribute to all $HTDog holders passively"},
]
export default  function HTDog() {
    useWeb3Context({onEth: onEth})

    function onEth(eth) {
        eth.request(registerEthereumChain("bscChainTestNet"))
    }

    return (
        <>
            <Row>
                <Image className={"m-auto"} src={logo} style={{maxWidth:"100%",width:300}}/>
            </Row>
            <Jumbotron className={"bg-dark p-5 mb-3 rounded"} style={{opacity: 0.8}}>
                <h1>HTDog</h1>
                <p>
                    HTDog is a simple hero unit, a simple jumbotron-style component for calling
                    extra attention to featured content or information.
                    <br/>
                    HTDog is a simple hero unit, a simple jumbotron-style component for calling
                    extra attention to featured content or information.
                </p>
                <p>
                    <Button variant="warning" href={pancakeAddressTest} target={"_blank"}>Swap on pancake</Button>
                </p>
            </Jumbotron>
            <Row>
                <Col>
                    <ClainBNBComponent></ClainBNBComponent>
                </Col>
                <Col sm={"auto"}>
                    <AddTokenToWalletComponent></AddTokenToWalletComponent>
                </Col>
            </Row>
            <Row>
                {tokenStyle.map((item,idx)=>(
                    <Col md={4} key={idx}>
                        <Card
                            bg={"dark"}
                            text={'light'}
                            // style={{ width: '18rem' }}
                            className="mb-2"
                            style={{zIndex:-1,opacity:0.8}}
                        >
                            <Card.Header>{item.desc}</Card.Header>
                            {/*<Card.Img variant="top" src={item.logo} />*/}
                            <span className={"text-center"}>{item.componet}</span>
                            <Card.Body>
                                <Card.Title>{item.title}</Card.Title>
                                <Card.Text>
                                    {item.content}
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>
        </>
    )
}



