export function registerEthereumChain(chain) {
    return {
        "jsonrpc": "2.0",
        "method": "wallet_addEthereumChain",
        "params": [
            chainInfo[chain]
        ],
    }
    // window.ethereum.request($)
}
export let useABI = () => {
    return {demoIcoABI: htdogAbi, demoContractAddress:demoContractAddress}
    const [abi,setAbi] = React.useState({demoIcoABI:[],ERC20TokenABI:[],ERC20TokenBIN:""})
    let url = "http://localhost:8099/btc/abi.json"
    React.useEffect(()=>{
        fetch(url).then(r=>r.json()).then(res=>{
            setAbi(res)
            console.log(res);
        })
    },[])
    return abi
}

