
import React from 'react'
import {Tabs} from 'react-bootstrap'
import Tab from 'react-bootstrap/Tab'

import {tokenConfig} from './tokenConfig'


export default function Token() {
    const [key, setKey] = React.useState(tokenConfig.defaultActiveKey)

    return (
        <Tabs
            activeKey={key}
            onSelect={(k) => setKey(k)}
            className="m-3"
        >
            {tokenConfig.tabs.map((tmp,i) => {
                return <Tab key={i} eventKey={tmp.key} title={tmp.title}>{tmp.content}</Tab>
            })}
        </Tabs>
    );
}



