
import React from 'react'
import {Button, Col, Row, InputGroup, FormControl, Spinner} from 'react-bootstrap'

import {useABI} from '../../assets/config'

import useWeb3Context from '../metamask/useWeb3Context'

export default function ClainBNBComponent(props) {
    // const {tokenAddress} = props;
    const abi = useABI()
    let tokenAddress = abi.demoContractAddress
    const {web3, accounts, callToken} = useWeb3Context();
    const contract = React.useRef()

    const [valEscrow, setValEscrow] = React.useState(0)
    const [visualEscrow, setVisualEscrow] = React.useState(true)
    const [visualSpinner, setVisualSpinner] = React.useState(false)
    const [visualWithdraw, setVisualWithdraw] = React.useState(false)

    React.useEffect(()=>{
        if (web3 != null && web3.eth != null) {
            // console.log("web3.eth: ", web3.eth)
            contract.current = new web3.eth.Contract(abi.demoIcoABI, tokenAddress)
        }
    }, [web3])


    async function  btnRefreshEscrow() {
        console.log("btnRefreshEscrow - accounts: ",accounts);
        setVisualSpinner(true)
        setVisualEscrow(false)
        contract.current.methods.calculateBNBReward(accounts[0]).call().then((balance)=>{
            console.log("escrow balance: " + balance)
            setValEscrow(balance)
            if (balance>0) {
                setVisualEscrow(false)
                setVisualSpinner(false)
                setVisualWithdraw(true)
            } else {
                setVisualEscrow(true)
                setVisualSpinner(false)
                setVisualWithdraw(false)
            }
        // }).catch((err)=>console.log(err.message))
        }).catch(console.error)
    }

    async function btnCallClick() {
        callToken(tokenAddress, contract.current.methods.claimBNBReward()).then((txHash)=>{
            // callToken(contract.methods.buyTokens(), val).then((txHash)=>{
            console.log("success, tx hash is: "+txHash)
            btnRefreshEscrow()
        }).catch((err)=>console.log(err.message))
    }

    return <Row className={"mb-3"}>
        <Col>
        </Col>
        <Col xs={"auto"}>
            {/*BNB Reward: {valEscrow}*/}
            {/*<Button onClick={btnRefreshEscrow} variant={"outline-warning"} className={visualEscrow?"visually-hidden":""+" ms-1"}>Refresh</Button>*/}
            {/*<Button onClick={btnCallClick} variant={"warning"} className={!visualEscrow?"visually-hidden":""} disabled={visualEscrow}>Withdraw</Button>*/}

            {/*<Button onClick={btnRefreshEscrow} variant={"outline-warning"} className={"me-1"}>Refresh</Button>*/}
            {/*<Button onClick={btnCallClick} variant={"warning"} disabled={true}>Claim</Button>*/}

            <InputGroup>
                <Button variant={"outline-warning"}>BNB Rewards</Button>
                <FormControl
                    value={valEscrow}
                    readOnly
                    placeholder={"please refresh"}
                />
                    <Button variant="outline-warning" className={!visualSpinner?"visually-hidden":""} disabled>
                        <Spinner
                            as="span"
                            animation="grow"
                            size="sm"
                            role="status"
                            aria-hidden="true"
                        />
                        Loading...
                    </Button>
                    <Button onClick={btnRefreshEscrow} variant={"warning"} className={!visualEscrow?"visually-hidden":""}>Refresh</Button>
                    <Button onClick={btnCallClick} variant={"warning"} className={!visualWithdraw?"visually-hidden":""}>Withdraw</Button>

            </InputGroup>
        </Col>

    </Row>
}


