import {Link, BrowserRouter as Router, Route, Switch} from 'react-router-dom'
import {Container, Navbar, Nav} from "react-bootstrap"
import useWeb3Context from './metamask/useWeb3Context'
import {logo} from '../assets/config'
import Mask from "./Mask";
import React from "react";
import AppRouter from "./Router_demo";
import Token from "./contractPage/Token/Token";
import Fanzone from "./fz/Fanzone";
import Space from './space/Space'
import HTDog from './space/HTDog'

const routerNavList = {
    // "/": "Home",
    // "/Token": "Token",
    // "/Fanzone": "Fanzone",
    // "/Space": "Space",
    // "/HTDog": "HTDog",
}
const routes = {
    '/': () => <HTDog/>,
    '/Token': () => <Token/>,
    // '/Token/TokenERC20' :()=> <TokenERC20 />,
    // '/Token/Deploy' :()=> <TokenDeploy />,
    '/Fanzone': () => <Fanzone/>,
    '/Space': () => <Space/>,
    '/HTDog': () => <HTDog/>,
};
export default function Layout() {
    const {OnboardingButton} = useWeb3Context({onFinish: maskHide})

    const [maskVisual, setMaskVisual] = React.useState("")

    function maskHide() {
        setMaskVisual("visually-hidden")
    }

    return (
        <div className={`text-light vh-100`}>
            <Router>
                <Navbar bg="dark" variant="dark" expand="lg">
                    <Container>
                        {/*<Navbar.Toggle aria-controls="navbarScroll"/>*/}
                        <Link key={0} className={"navbar-brand"} to="/">
                            <img alt="" src={logo} width="30" height="30" className="d-inline-block align-top"/>{' '}
                            HTDog
                        </Link>

                        {/*<Navbar.Collapse id="navbarScroll">*/}
                        {/*    <Nav style={{maxHeight: '100px'}} navbarScroll>*/}
                        {/*        {*/}
                        {/*            Object.keys(routerNavList).map((k,i) => (*/}
                        {/*                <Link key={i+1} className={"nav-link"} to={k}>{routerNavList[k]}</Link>))*/}
                        {/*        }*/}
                        {/*    </Nav>*/}
                        {/*</Navbar.Collapse>*/}
                        <OnboardingButton/>
                    </Container>
                </Navbar>

                <Container>
                    <Mask maskVisually={maskVisual}/>
                    <Switch>
                        {Object.keys(routes).map((k,i) => (
                            <Route key={i} path={k} exact component={routes[k]}/>
                        ))}
                    </Switch>
                </Container>
            </Router>
        </div>
    )
}



