
import React from 'react'
import {Button} from 'react-bootstrap'

import {TokenInfo} from '../../assets/config'

import useWeb3Context from '../metamask/useWeb3Context'

export default function AddTokenToWalletComponent() {
    const {eth} = useWeb3Context();

    async function handAddTokenToWallet() {
        const tokenAddress = TokenInfo.contractAddress;
        const tokenSymbol = TokenInfo.tokenSymbol;
        const tokenDecimals = TokenInfo.tokenDecimals;
        const tokenImage = TokenInfo.tokenImage;

        try {
            // wasAdded is a boolean. Like any RPC method, an error may be thrown.
            const wasAdded = await eth.request({
                method: 'wallet_watchAsset',
                params: {
                    type: 'ERC20', // Initially only supports ERC20, but eventually more!
                    options: {
                        address: tokenAddress, // The address that the token is at.
                        symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
                        decimals: tokenDecimals, // The number of decimals in the token
                        image: tokenImage, // A string url of the token logo
                    },
                },
            });

            if (wasAdded) {
                console.log('Thanks for your interest!');
            } else {
                console.log('Your loss!');
            }
        } catch (error) {
            console.log(error);
        }
    }

    return <Button onClick={handAddTokenToWallet} className={"float-end"} variant={"warning"}>Add Token To Wallet</Button>
}


