import React from 'react'

import TokenCommon from './TokenCommon'
import TokenERC20MetaData from './TokenERC20MetaData'
import {Col, Form, InputGroup, Row} from 'react-bootstrap'
import {TokenInfo, useABI, swapRouters} from "../../../assets/config";

import useWeb3Context from '../../metamask/useWeb3Context'

export default function SafeMoon() {
    let abi = useABI()
    const {web3, accounts} = useWeb3Context()
    const [formValues, setFormValues] = React.useState({TokenName:"",TokenSymbol:"",TotalSupply:0,
    Dividend:0,Liquidity:0,TransactionLimit:-1});

    function onDeploy(chain, setTxHash, setDeployResult, setDeployContractAddress) {
        console.log("deploy arguments: ", formValues)
        let w3 = new web3.eth.Contract(abi["SafeMoonABI"])

        w3.deploy({
            data: abi[abi["SafeMoonBIN"]],
            arguments: [
                formValues.TokenName, formValues.TokenSymbol, formValues.TotalSupply,
                formValues.Dividend, formValues.Liquidity, formValues.TransactionLimit,
                swapRouters[chain]
            ]
        })
            .send({
                from: accounts[0],
                gasPrice: TokenInfo.gasPrice,
                gas: 4712388,
            }, function (error, transactionHash) {
                console.log("deploy error: ", error);
                setTxHash(transactionHash)
            })
            .on('error', function(error){
                console.log("deploy error: ", error);
                setDeployResult("deploy fail")
            })
            .on('transactionHash', function(transactionHash){setTxHash(transactionHash)})
            .on('receipt', function(receipt){
                console.log(receipt.contractAddress) // contains the new contract address
                setDeployContractAddress(receipt.contractAddress)
                setDeployResult("deploy success")
            })
            // .on('confirmation', function(confirmationNumber, receipt){ ... })
            .then(function(newContractInstance){
                // setDeployResult(DeploySuccess())
                console.log(newContractInstance.options) // instance with the new contract address
            });

    }
    return (
        <TokenCommon
            // onFormValues={[formValues.TokenName, formValues.TokenSymbol, formValues.TotalSupply]}
            onFormValues2={formValues}
            onDeploy={onDeploy}
            // onFormValues={formValues}
            onAbiInfo={{abi: "ERC20TokenABI", bin: "ERC20TokenBIN"}}>
            <TokenERC20MetaData formValues={formValues} setFormValues={setFormValues}/>

            <Row className="mb-3">
                <Form.Group as={Col} md="4">
                    <Form.Label>Token Dividend Rate</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="eg: 0.1"
                        // defaultValue={formValues.TokenName}
                        // name={"TokenName"}
                        onChange={evt => {
                            setFormValues(state => ({...state, Dividend: evt.target.value}))
                        }}
                    />
                    <Form.Text className="text-muted">
                        Automatically generating further liquidity on Swap
                    </Form.Text>
                </Form.Group>
                <Form.Group as={Col} md="4">
                    <Form.Label>eth and liquid pool rate</Form.Label>
                    <Form.Control
                        required
                        type="text"
                        placeholder="eg: 0.1 = (0.05 eth + 0.05 liquidity)"
                        // defaultValue={formValues.TokenSymbol}
                        // name={"TokenSymbol"}
                        onChange={evt => {
                            setFormValues(state => ({...state, Liquidity: evt.target.value}))
                        }}
                    />
                    <Form.Text className="text-muted">
                        Eth reward redistribute to all $Token holders passively
                    </Form.Text>
                </Form.Group>
                <Form.Group as={Col} md="4">
                    <Form.Label>Transaction Limit</Form.Label>
                    <InputGroup hasValidation>
                        {/*<InputGroup.Text id="inputGroupPrepend">@</InputGroup.Text>*/}
                        <Form.Control
                            type="number"
                            placeholder="21000000"
                            aria-describedby="inputGroupPrepend"
                            required
                            // name={"TotalSupply"}
                            // defaultValue={formValues.TotalSupply}
                            onChange={evt => {
                                setFormValues(state => ({...state, TransactionLimit: evt.target.value}))
                            }}
                        />
                        <Form.Text className="text-muted">
                            Inbuilt Anti-Whale mechanism and drastic dump prevention
                        </Form.Text>
                    </InputGroup>
                </Form.Group>
            </Row>
        </TokenCommon>
    );
}



