import React from 'react'

import TokenCommon from './TokenCommon'
import TokenERC20MetaData from './TokenERC20MetaData'
import {Col, Form, Button, InputGroup, Row} from "react-bootstrap";


export default function TokenERC777() {
    const [formValues, setFormValues] = React.useState({TokenName:"",TokenSymbol:"",TotalSupply:0,DefaultOperators:[]});
    const [operators, setOperators] = React.useState([""]);
    console.log("operators-operators: ", operators)

    function addOperator() {
        setOperators((state)=>([...state, ""]))
        setFormValues(state => ({...state, DefaultOperators: operators}))
    }
    function delOperator(index) {
        if (operators.length > 1) {
            setOperators((state) =>(state.filter((tmp,j)=>j!==index)))
            setFormValues(state => ({...state, DefaultOperators: operators}))
        }
    }
    function editOperator(index, val) {
        setOperators((state) => (state.map((item, j) => {return (j === index)? val: item})))
        setFormValues(state => ({...state, DefaultOperators: operators}))
    }

    return (
        <TokenCommon
            onFormValues={[formValues.TokenName, formValues.TokenSymbol, formValues.TotalSupply]}
            // onFormValues={formValues}
            onAbiInfo={{abi: "ERC20TokenABI", bin: "ERC20TokenBIN"}}>
            <TokenERC20MetaData formValues={formValues} setFormValues={setFormValues}/>

            <Form.Label>Default Operators
                <Button onClick={addOperator} variant={"outline-primary"} size={"sm"}>+1</Button>
            </Form.Label>
            <Row>
                {operators.map((val, i) => (
                    <Form.Group key={i} as={Col} md="4" className={"mb-3"}>
                        {/*<Form.Label>Default Operators <button type={"button"} className={"btn btn-outline-primary btn-clipboard"}>+1</button></Form.Label>*/}
                        <InputGroup>
                            <Form.Control
                                // required
                                type="text"
                                placeholder="Default Operators(eg: 0xa12...)"
                                // name={"TokenName"}
                                value={val}
                                onChange={evt => {
                                    editOperator(i, evt.target.value)
                                }}
                            />

                            <Button onClick={()=>delOperator(i)} variant={"danger"} size={"sm"}>-</Button>
                        </InputGroup>
                    </Form.Group>
                ))}
            </Row>
        </TokenCommon>
    );
}



