import React from 'react'
import {Button, Form, Row} from 'react-bootstrap'

import {chainList, TokenInfo, chainInfo, registerEthereumChain, useABI} from '../../../assets/config'

import useWeb3Context from '../../metamask/useWeb3Context'


export default function TokenCommon(props) {
    let abi = useABI()
    // const {onFormValues, onAbiInfo} = props
    const {onFormValues, onFormValues2, onAbiInfo, onDeploy} = props
    // const [onAbiInfo] = React.useState(props.onAbiInfo)
    console.log("onFormValues, onAbiInfo : ", onFormValues, onAbiInfo)
    console.log("onFormValues2 : ", onFormValues2)

    const [validated, setValidated] = React.useState(false);
    // const [formValues, setFormValues] = React.useState({TokenName:"",TokenSymbol:"",TotalSupply:0,Chain:"localNet"});
    const [formValues, setFormValues] = React.useState({Chain:"localNet"});
    const [deployResult, setDeployResult] = React.useState("not deploy yet")
    const [deployContractAddress, setDeployContractAddress] = React.useState("")
    const [txHash, setTxHash] = React.useState("")
    const {web3, accounts, eth} = useWeb3Context()

    const handleSubmit = (event) => {
        const form = event.currentTarget;
        console.log("form formValues: ", formValues, onFormValues);
        console.log("form event: ", form.checkValidity());

        if (form.checkValidity() === false) {
            console.log(1111111111);
            event.preventDefault();
            event.stopPropagation();
            setValidated(true);
            return false
        }
        console.log(222222222222);
        event.preventDefault();
        event.stopPropagation();

        if (onDeploy != null) onDeploy(formValues.Chain, setTxHash, setDeployResult, setDeployContractAddress)
        else deploy()
    };

    function deploy() {
        console.log("deploy arguments: ", onFormValues)
        let w3 = new web3.eth.Contract(abi[onAbiInfo.abi])

        w3.deploy({
            data: abi[onAbiInfo.bin],
            arguments: [...onFormValues]
        })
            .send({
                from: accounts[0],
                gasPrice: TokenInfo.gasPrice,
                gas: 4712388,
            }, function (error, transactionHash) {
                console.log("deploy error: ", error);
                setTxHash(transactionHash)
            })
            .on('error', function(error){
                console.log("deploy error: ", error);
                setDeployResult("deploy fail")
            })
            .on('transactionHash', function(transactionHash){setTxHash(transactionHash)})
            .on('receipt', function(receipt){
                console.log(receipt.contractAddress) // contains the new contract address
                setDeployContractAddress(receipt.contractAddress)
                setDeployResult("deploy success")
            })
            // .on('confirmation', function(confirmationNumber, receipt){ ... })
            .then(function(newContractInstance){
                // setDeployResult(DeploySuccess())
                console.log(newContractInstance.options) // instance with the new contract address
            });

    }

    return (
        <>
            <Form noValidate validated={validated} onSubmit={handleSubmit}>
                {props.children}

                <Form.Group className="mb-3">
                    <Form.Label>BlockChain Network</Form.Label>
                    {chainList.map((chain) => (
                        <div key={`default-${chain.name}`} className="mb-3 me-2">
                            <Form.Check
                                type="radio"
                                label={`${chain.name} (${chain.description})`}
                                id={`${chain.name}-radio-id`}
                                name="Chain"
                                value={chain.name}
                                required
                                onChange={evt=>{
                                    console.log("radio evt: ", evt.target.value);
                                    setFormValues(state => ({...state,Chain:evt.target.value}))
                                    console.log(registerEthereumChain(evt.target.value));
                                    eth.request(registerEthereumChain(evt.target.value))
                                }}
                            />
                        </div>
                    ))}
                </Form.Group>
                <Button type="submit">Submit form</Button>

                <Row className={"bg-light text-dark mt-3 m-1 p-3 rounded text-break"}>
                    <p>deploy result: {deployResult}</p>
                    <p>deploy address: <a href={chainInfo[formValues.Chain].blockExplorerUrls[0] + "/address/" + deployContractAddress}>{deployContractAddress}</a></p>
                    <p>deploy txHash: <a href={chainInfo[formValues.Chain].blockExplorerUrls[0] + "/tx/" + txHash}>{txHash}</a></p>
                </Row>
            </Form>
        </>
    );
}




