import Spinner from 'react-bootstrap/Spinner'
import React from "react";

export default function Mask(props) {
    const {maskVisually} = props
    return (
        <div id={"mask-box"} className={maskVisually}>
            <div id={"mask"}>
                <div className="text-mask">
                    <Spinner animation="border" variant="danger"/>
                    <span className={"text-danger"}>connect wallet first...</span>
                    <Spinner animation="border" variant="warning"/>
                    <span className={"text-warning"}>connect wallet first...</span>
                    <Spinner animation="border" variant="success"/>
                    <span className={"text-success"}>connect wallet first...</span>
                </div>
            </div>
        </div>
    )
}



